"use client";

import BoletinPDF from "./boletin/BoletinPDF";
import { useAuth } from "../../context/authContext";
import styles from "../../styles/estudiante-dashboard.module.css";
import { useState } from "react";
import Link from 'next/link';

export default function EstudianteDashboard() {
  const { user, loading } = useAuth();
  const [cicloId] = useState(1);

  if (loading) return <div className={styles.loadingContainer}><h2>Cargando...</h2></div>;
  if (!user) return <div className={styles.loadingContainer}><h2>Bienvenido a AulaUnida</h2><p>Por favor inicia sesi칩n.</p></div>;

  // Datos de ejemplo (se pueden reemplazar por fetch reales)
  const tareas = [
    { titulo: 'Resolver ejercicios de 치lgebra', asignatura: 'Matem치ticas', vence: 'Vence: 2 d칤as' },
    { titulo: 'Investigar sobre ecosistemas', asignatura: 'Ciencias', vence: 'Vence: 5 d칤as' }
  ];
  const clases = [
    { materia: 'Matem치ticas', prof: 'Profesor: Dr. Mart칤nez', hora: '10:00 AM' },
    { materia: 'Ciencias', prof: 'Profesora: Dra. Lopez', hora: '11:30 AM' }
  ];
  const notis = [
    { titulo: 'Examen de Matem치ticas', detalle: 'Fecha: 15 de mayo', tipo: 'red' },
    { titulo: 'Entrega de proyecto de Ciencias', detalle: 'Fecha: 20 de mayo', tipo: 'blue' },
    { titulo: 'Nuevas calificaciones disponibles', detalle: 'Asignatura: Historia', tipo: 'green' }
  ];

  return (
    <div className={styles.wrapper}>
      <aside className={styles.sidebar}>
        <div className={styles.profile}>
          <img src="/default-avatar.png" alt="avatar" className={styles.avatar} />
          <div>
            <div className={styles.name}>{user.displayName || user.email}</div>
            <div className={styles.role}>Estudiante</div>
          </div>
        </div>
        <nav className={styles.nav}>
          <Link href="/estudiante" className={styles.navItem + ' ' + styles.active}>Inicio</Link>
          <Link href="/estudiante/asignaturas" className={styles.navItem}>Asignaturas</Link>
          <Link href="/estudiante/notas" className={styles.navItem}>Notas y Asistencia</Link>
          <Link href="/estudiante/boletin" className={styles.navItem}>Bolet칤n</Link>
          <Link href="/estudiante/anuncios" className={styles.navItem}>Anuncios</Link>
        </nav>
      </aside>

      <main className={styles.main}>
        <header className={styles.header}>
          <h1>Panel de Estudiante</h1>
          <p className={styles.subtitle}>Bienvenida de nuevo, {user.displayName?.split(' ')[0] || 'estudiante'}.</p>
        </header>

        <section className={styles.contentRow}>
          <div className={styles.leftCol}>
            <div className={styles.card}>
              <h3>Tareas Pendientes</h3>
              <div className={styles.taskList}>
                {tareas.map((t, i) => (
                  <div key={i} className={styles.taskItem}>
                    <div className={styles.taskIcon}>游늯</div>
                    <div style={{flex:1}}>
                      <div className={styles.taskTitle}>{t.titulo}</div>
                      <div className={styles.taskSub}>{t.asignatura}</div>
                    </div>
                    <div className={styles.taskDue}>{t.vence}</div>
                  </div>
                ))}
              </div>
            </div>

            <div className={styles.card} style={{marginTop: 18}}>
              <h3>Pr칩ximas Clases</h3>
              <div className={styles.classList}>
                {clases.map((c, i) => (
                  <div key={i} className={styles.classItem}>
                    <div>
                      <div className={styles.classTitle}>{c.materia}</div>
                      <div className={styles.classSub}>{c.prof}</div>
                    </div>
                    <div className={styles.classTime}>{c.hora}</div>
                  </div>
                ))}
              </div>
            </div>
          </div>

          <aside className={styles.rightCol}>
            <div className={styles.cardCol}>
              <h3>Notificaciones</h3>
              <div className={styles.notis}>
                {notis.map((n, i) => (
                  <div key={i} className={styles.notiItem}>
                    <div className={styles.notiIcon} data-type={n.tipo}>游댒</div>
                    <div>
                      <div className={styles.notiTitle}>{n.titulo}</div>
                      <div className={styles.notiSub}>{n.detalle}</div>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </aside>
        </section>
      </main>
    </div>
  );
}